.about__top {
    height: 35rem;
    background-color: $color-grey-dark;
    color: #fff;
}

.about__header {
    display: flex;
    justify-content: space-between;
}

.about__header-text {
    margin: 12rem 0 0 10rem;
}

.about__heading {
    margin: 0;
    font-size: 5rem;
}

.about__quote {
    font-size: 1.6rem;
    margin: 0;
    color: #b3b3b3;
}

.about__quote-auther {
    font-size: 1.2rem;
}

.about__img {
    margin: 7rem 5rem 0 0;
}

.about__img img {
    width: 600px;
    height: 400px;
    object-fit: cover;
    // filter: brightness(90%);
}

.about__info {
    background-color: #e8e8e8;
    height: 500px;
    padding: 5rem 2rem;

    display: flex;
    justify-content: center;
    align-items: center;
}

.about__text {
    margin: 0;
    width: 600px;
    font-size: 1.6rem;
    color: #6f6f6f;
}