*,
*::before,
*::after {
    box-sizing: inherit;
}

html {
    font-size: 62.5%;
}

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    font-family: $font-text;
    color: $color-grey-dark;
    line-height: 1.6;
}

.container {
    max-width: 120rem;
    margin: 0 auto;
}

.section {
    background-color: #e9e9e9;
    padding: 1px 0; 
}

.error_msg {
    color: red;
    margin: 0;
    font-size: 1.2rem;
}